  position: fixed;
  top: 0;
  z-index: 9999;
  background: #fff;
}

.loader {
  display: inline-block;
  width: 40px;
  height: 40px;
  position: fixed;
  z-index: 10000;
  border: 4px solid #4C40ED;
  top: 50%;
  animation: loader 2s infinite ease;
  left: 50%;
  border-radius: 5px;
}

.loader-inner {
  vertical-align: top;
  display: inline-block;
  width: 100%;
  background-color: #4C40ED;
  animation: loader-inner 2s infinite ease-in;
}

@keyframes loader {
  0% {
    transform: rotate(0deg);
  }
  25% {
    transform: rotate(180deg);
  }
  50% {
    transform: rotate(180deg);
  }
  75% {
    transform: rotate(360deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes loader-inner {
  0% {
    height: 0%;
  }
  25% {
    height: 0%;
  }
  50% {
    height: 100%;
  }
  75% {
    height: 100%;
  }
  100% {
    height: 0%;
  }
}
